<template>
  <div>
    <mt-header title="绑定手机" style="background: #fff;color: #1B1B1B;">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div class="form">
      <div style="text-align: center;margin: 20px 0">
        <img src="../../../static/img/my/shield.png" style="width: 40px;height: auto;vertical-align: middle;margin: 0 10px">
        <span style="color: #1B1B1B;font-size: 16px;">为了您的账户安全请绑定手机</span>
      </div>
      <div class="list">
        <div class="item" style="position: relative">
          <input type="text" placeholder="请输入您的手机号" style="width: calc(100% - 115px)">
          <el-button plain style="position: absolute;padding: 7px 15px;right: 5px;top: 8px;color:#AAAAAA;">获取验证码</el-button>
        </div>
        <div class="item">
          <input type="number" placeholder="请输入验证码">
        </div>
      </div>
      <div style="margin: 20px 0;padding: 0 15px;">
        <el-button type="primary" round style="width: 100%;margin: 15px 0;border-radius: 5px" @click="go('/registerlecturer')"  >立即绑定</el-button>

      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      };
    },
    mounted(){

    },
    methods: {

    }
  }

</script>



<style lang="less" scoped>
  .form{
    border-top: 1px solid #f2f2f2;
    .list{
      padding: 0 15px;
      margin-top: 30px;
      .item{
        border-bottom: 1px solid #f2f2f2;
        input{
          outline: none;
          border: 0;
          width: 100%;
          height: 35px;
          padding: 5px 0 5px 5px;
        }
      }
    }
  }
  .bottom{

    position: absolute;bottom: 0;
    width: 100%;
    text-align: center;
    color: #959595;
    font-size: 12px;
    .title{
      position: relative;
      .txt{
        line-height: 30px;
      }
      .txt:before{
        content: '';
        position: absolute;;left: 0px;
        top:15px;
        width: calc(50% - 50px);
        height: 1px;
        background: #959595;
      }
      .txt:after{
        content: '';
        position: absolute;;right: 0px;
        top:15px;
        width: calc(50% - 50px);
        height: 1px;
        background: #959595;
      }
    }
  }
</style>




